import React from 'react';
import styles from './ProductGrid.module.scss';

import { mapLooksToStyles } from 'utils';

class ProductGrid extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      categories: [
        'Flyer',
        'Faltblätter',
        'Postkarten',
        'Klappkarten',
        'Multiloft',
        'Visitenkarten',
        'Broschüren',
        'Plakate',
        'Briefpapier',
        'Briefumschläge',
        'Mappen',
        'Aufkleber',
        'Stickerkarten',
        'Verpackungen',
        'Werbeartikel',
        '3D-Lack',
        'Proof',
        'Mustermappe'
      ]
    }
  }
  render () {
    // map looks to a styles[look] string
    const lookStyles = mapLooksToStyles(this.props.looks, styles);

    return (
      <div className={`${styles.productGrid} ${lookStyles}`}>
        <h2>Wählen Sie ihr Produkt</h2>
        {this.renderCategories()}
      </div>
    );
  }
  renderCategories () {
    const { categories } = this.state;

    const items = categories.map((item, index) => {
      const img = require(`./assets/${item}.svg`);

      return (
        <div key={index} className={styles.category}>
          <img src={img} alt={item}/>
          <a href="">{item}</a>
        </div>
      );
    });

    return (
      <div className={styles.categoriesContainer}>
        {items}
      </div>
    )

  }
}

export default ProductGrid;
