import React from 'react';

import { mapLooksToStyles } from 'utils';

import styles from './Slider.module.scss';
import Button from '../Button';

import img from './assets/img.png';

const config = {
  slideDelay: 5000
};

class Slider extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeSlide: 0,
      slidesPerScreen: 2,
      slides: [
        {
          color: 'C',
          class: 'isCenter',
          backgroundImage: img,
          content: (
            <div></div>
          )
        },
        {
          color: 'Y',
          class: 'isCenter',
          content: (
            <div>
              <h1>
                Lorem Ipsum
                dolor est rerum.
              </h1>
              <p className="g-fontText1">
                Ublicaequam nostaribem pulegitemur
                blicaequam nostaribem
                tideps, quo caetellegocondicit?
              </p>
              <Button looks="hasYellowText">
                Jetzt bestellen
              </Button>
            </div>
          )
        },
        {
          color: 'K',
          class: 'isCenter',
          backgroundImage: img,
          content: (
            <div></div>
          )
        },
        {
          color: 'M',
          class: 'isCenter',
          content: (
            <div>
              <h1>
                Lorem Ipsum
                dolor est rerum.
              </h1>
              <p className="g-fontText1">
                Ublicaequam nostaribem pulegitemur
                blicaequam nostaribem
                tideps, quo caetellegocondicit?
              </p>
              <Button looks="hasYellowText">
                Jetzt bestellen
              </Button>
            </div>
          )
        },
        {
          color: 'K',
          class: 'isCenter',
          content: (
            <div>
              <h1>
                Lorem Ipsum
                dolor est rerum.
              </h1>
              <p className="g-fontText1">
                Ublicaequam nostaribem pulegitemur
                blicaequam nostaribem
                tideps, quo caetellegocondicit?
              </p>
              <Button looks="isInverted hasCyanBackground hasWhiteText">
                Jetzt bestellen
              </Button>
            </div>
          )
        }
      ]
    }

    this.handleInterval = this.handleInterval.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  intervalId = null;

  componentDidUpdate() {
    this.handleInterval();
  }
  componentDidMount() {
    this.handleInterval();

    const mql = window.matchMedia('(max-width: 1024px)');
    if (mql.matches) {
      this.setState({ slidesPerScreen: 1 });
    }

  }
  componentWillUnmount() {
  }

  handleInterval () {
    if (this.intervalId) {
      window.clearInterval(this.intervalId);
      this.intervalId = null;
    }

    this.intervalId = window.setInterval(() => {
      this.slide('right');
    }, config.slideDelay);
  }

  handleResize () {

  }

  handleClick() {
    this.slide('right');
  }

  slide (direction) {
    let { activeSlide, slidesPerScreen, slides } = this.state;
    if (direction === 'right') {
      activeSlide++;
    }
    else if (direction === 'left') {
      activeSlide--;
    }
    else if (!isNaN(direction)) {
      activeSlide = direction;
    }
    if (activeSlide > slides.length - slidesPerScreen) { // minus slidesPerScreen so we don't scroll too far when there's two slides per screen
      activeSlide = 0;
    }
    this.setState({ activeSlide });
  }

  render () {
    // map looks to a styles[look] string
    const lookStyles = mapLooksToStyles(this.props.looks, styles);
    const { activeSlide, slidesPerScreen } = this.state;

    return (
      <div className={`${styles.slider} ${lookStyles}`}>

        {/* SlideBox */}
        <div className={styles.slideBox}
          style={{transform: `translate(-${activeSlide * 100 / slidesPerScreen}%, 0)`}}>
          {this.renderSlides()}
        </div>

        {/* Bubbles */}
        <div className={styles.bubblesContainer}>
          {this.renderBubbles()}
        </div>

      </div>
    );
  }

  renderSlides () {
    const { slides } = this.state;

    return slides.map((slide, index) => {
      const slideStyles = `${styles.slide} ${styles['is' + slide.color]} ${styles[slide.class]}`;
      const inlineStyles = slide.backgroundImage ? { background: `url(${slide.backgroundImage}) center`, backgroundSize: 'cover'} : {};
      return (
        <div key={index} className={slideStyles}
          style={inlineStyles}>
          {slide.content}
        </div>
      );
    })
  }

  renderBubbles () {
    const { activeSlide, slidesPerScreen, slides } = this.state;
    const bubblesCount = slides.length + 1 - slidesPerScreen; // substract 1 because we have 2 slides on one screen

    let bubbles = [];

    for (let i = 0; i < bubblesCount; i++) {
      bubbles.push((
        <div key={i}
          className={`${styles.bubble} ${i === activeSlide ? styles.isActive : ''}`}
          onClick={() => { this.slide(i); }}>
        </div>
      ));
    }

    return bubbles;
  }
}

export default Slider;
