import React from 'react';
import styles from './LogoWall.module.scss';

import { mapLooksToStyles } from 'utils';

import iso from './assets/iso.png';
import trusted from './assets/trusted.png';

class LogoWall extends React.Component {
  render () {
    // map looks to a styles[look] string
    const lookStyles = mapLooksToStyles(this.props.looks, styles);

    return (
      <div className={`${styles.logoWall} ${lookStyles}`}>
        <div>
          <h4>Konstenfreie Zahlung</h4>
          <img src={iso} className={styles.smallImage} alt="iso"/>
          <img src={iso} className={styles.smallImage} alt="iso"/>
          <img src={iso} className={styles.smallImage} alt="iso"/>
          <img src={iso} className={styles.smallImage} alt="iso"/>
        </div>
        <div>
          <h4>Nachhaltig</h4>
          <img src={iso} className={styles.smallImage} alt="iso"/>
          <img src={iso} className={styles.smallImage} alt="iso"/>
          <img src={iso} className={styles.smallImage} alt="iso"/>
        </div>
        <div>
          <h4>Zertifizerter Shop</h4>
          <img src={trusted} className={styles.image} alt="trusted shop"/>
        </div>
      </div>
    );
  }
}

export default LogoWall;
